import axios from 'axios';

// ACTION TYPES
const UPDATE_NAME = 'UPDATE_NAME';

// INITIAL STATE
const initialState = '';

// ACTION CREATORS
export const updateName = name => {
  return {
    type: UPDATE_NAME,
    name: name,
  };
};

